<template>
	<view class="user-center">
		<!-- Áî®Êà∑‰ø°ÊÅØÂç°Áâá -->
		<view class="user-card">
			<!-- Âä†ËΩΩ‰∏≠ÁöÑÈ™®Êû∂Â±è -->
			<view v-if="loading" class="skeleton-card">
				<view class="skeleton-avatar"></view>
				<view class="skeleton-info">
					<view class="skeleton-line skeleton-name"></view>
					<view class="skeleton-line skeleton-id"></view>
				</view>
			</view>
			
			<!-- Âä†ËΩΩÂÆåÊàêÊòæÁ§∫ÁúüÂÆûÂÜÖÂÆπ -->
			<template v-else>
				<view class="user-avatar">
					<image v-if="userAvatar" :src="userAvatar" mode="aspectFill" />
					<image v-else src="/static/default-avatar.png" mode="aspectFill" />
				</view>
				<view class="user-info">
					<text class="user-name">{{ userInfo.nickname || userInfo.username || userInfo.mobile || 'Êú™ËÆæÁΩÆÊòµÁß∞' }}</text>
					<text class="user-id">ID: {{ userId }}</text>
				</view>
				<view class="user-role-badge" v-if="isAdmin">
					<text class="badge-text">ÁÆ°ÁêÜÂëò</text>
				</view>
			</template>
		</view>

		<!-- ÂäüËÉΩËèúÂçï -->
		<view class="menu-section">
			<view class="menu-title">ËØÑ‰º∞ÁÆ°ÁêÜ</view>
			<view class="menu-list">
				<view class="menu-item" @click="goToChildInfo">
					<view class="menu-item-icon">üìù</view>
					<text class="menu-item-text">Êñ∞Âª∫ËØÑ‰º∞</text>
					<text class="menu-item-arrow">‚Ä∫</text>
				</view>
				<view class="menu-item" @click="goToHistory">
					<view class="menu-item-icon">üìã</view>
					<text class="menu-item-text">ÂéÜÂè≤ËÆ∞ÂΩï</text>
					<text class="menu-item-arrow">‚Ä∫</text>
				</view>
			</view>
		</view>

		<!-- ÁÆ°ÁêÜÂëòÂäüËÉΩ -->
		<view class="menu-section" v-if="isAdmin">
			<view class="menu-title">ÁÆ°ÁêÜÂëòÂäüËÉΩ</view>
			<view class="menu-list">
				<view class="menu-item" @click="goToDashboard">
					<view class="menu-item-icon">üë®‚Äçüíº</view>
					<text class="menu-item-text">ÁÆ°ÁêÜÂëòÈù¢Êùø</text>
					<text class="menu-item-arrow">‚Ä∫</text>
				</view>
				<view class="menu-item" @click="goToInitQuestions">
					<view class="menu-item-icon">üîß</view>
					<text class="menu-item-text">ÂàùÂßãÂåñÈ¢òÁõÆ</text>
					<text class="menu-item-arrow">‚Ä∫</text>
				</view>
			</view>
		</view>

		<!-- Ë¥¶Âè∑ËÆæÁΩÆ -->
		<view class="menu-section">
			<view class="menu-title">Ë¥¶Âè∑ËÆæÁΩÆ</view>
			<view class="menu-list">
				<view class="menu-item" @click="goToUserInfo">
					<view class="menu-item-icon">üë§</view>
					<text class="menu-item-text">‰∏™‰∫∫ËµÑÊñô</text>
					<text class="menu-item-arrow">‚Ä∫</text>
				</view>
				<view class="menu-item" @click="goToChangePwd">
					<view class="menu-item-icon">üîí</view>
					<text class="menu-item-text">‰øÆÊîπÂØÜÁ†Å</text>
					<text class="menu-item-arrow">‚Ä∫</text>
				</view>
			</view>
		</view>

		<!-- ÈÄÄÂá∫ÁôªÂΩïÊåâÈíÆ -->
		<view class="logout-section">
			<button class="logout-button" @click="logout">ÈÄÄÂá∫ÁôªÂΩï</button>
		</view>

		<!-- ËøîÂõûÈ¶ñÈ°µ -->
		<view class="back-home-section">
			<text class="back-home-text" @click="goHome">ËøîÂõûÈ¶ñÈ°µ</text>
		</view>
	</view>
</template>

<script>
import { store, mutations } from '@/uni_modules/uni-id-pages/common/store.js'

export default {
	computed: {
		userInfo() {
			return store.userInfo || {}
		},
		// Ëé∑ÂèñÂ§¥ÂÉè URL
		userAvatar() {
			const info = store.userInfo
			if (!info) return ''
			
			// Â§öÁßçÂ§¥ÂÉèÂ≠óÊÆµÂÖºÂÆπ
			if (info.avatar) return info.avatar
			if (info.avatar_file && info.avatar_file.url) return info.avatar_file.url
			if (info.avatarUrl) return info.avatarUrl
			
			return ''
		},
		// Ëé∑ÂèñÁî®Êà∑ IDÔºàÁÆÄÂåñÊòæÁ§∫Ôºâ
		userId() {
			const info = store.userInfo
			if (!info || !info._id) return '-'
			return info._id.substring(0, 8) + '...'
		}
	},
	data() {
		return {
			isAdmin: false,
			loading: true
		}
	},
	onLoad() {
		this.checkLogin();
		// Á´ãÂç≥Â∞ùËØïÂä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
		this.loadUserInfo();
	},
	onShow() {
		// ÊØèÊ¨°ÊòæÁ§∫È°µÈù¢Êó∂Âà∑Êñ∞Áî®Êà∑‰ø°ÊÅØ
		this.loadUserInfo();
	},
	methods: {
		// Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
		checkLogin() {
			const token = uni.getStorageSync('uni_id_token');
			if (!token) {
				uni.showModal({
					title: 'ÈúÄË¶ÅÁôªÂΩï',
					content: 'ËØ∑ÂÖàÁôªÂΩï',
					showCancel: false,
					success: () => {
						uni.reLaunch({
							url: '/uni_modules/uni-id-pages/pages/login/login-withpwd'
						});
					}
				});
			}
		},
		
	// Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
	async loadUserInfo() {
		try {
			this.loading = true;
			
			// ÂÖà‰ªé token Ëß£ÊûêÂü∫Êú¨‰ø°ÊÅØÔºàÂø´ÈÄüÊòæÁ§∫Ôºâ
			const token = uni.getStorageSync('uni_id_token');
			if (token) {
				const tokenArr = token.split('.');
				if (tokenArr.length === 3) {
					try {
						const payload = JSON.parse(decodeURIComponent(escape(atob(tokenArr[1]))));
						const role = payload.role || [];
						this.isAdmin = Array.isArray(role) ? role.includes('admin') : role === 'admin';
						
						// Token ‰∏≠Â¶ÇÊûúÊúâÂü∫Êú¨‰ø°ÊÅØÔºåÂèØ‰ª•ÂÖàÊòæÁ§∫
						if (payload.uid) {
							console.log('[user-center] ‰ªé token Ëé∑ÂèñÂà∞ uid:', payload.uid);
						}
					} catch (e) {
						console.warn('[user-center] Ëß£Êûê token Â§±Ë¥•:', e);
					}
				}
			}
			
			// Â¶ÇÊûú store ‰∏≠Â∑≤ÊúâÁºìÂ≠òÊï∞ÊçÆÔºåÁ´ãÂç≥ÁªìÊùü loading
			if (store.userInfo && store.userInfo._id) {
				console.log('[user-center] ‰ªéÁºìÂ≠òÂä†ËΩΩÁî®Êà∑‰ø°ÊÅØ:', store.userInfo.nickname || store.userInfo.username);
				this.loading = false;
			}
			
			// ÂºÇÊ≠•Êõ¥Êñ∞ÂÆåÊï¥ÁöÑÁî®Êà∑‰ø°ÊÅØ
			await mutations.updateUserInfo();
			
			console.log('[user-center] Áî®Êà∑‰ø°ÊÅØÊõ¥Êñ∞ÂÆåÊàê:', store.userInfo);
			
		} catch (error) {
			console.error('[user-center] Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•:', error);
			uni.showToast({
				title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
				icon: 'none',
				duration: 2000
			});
		} finally {
			this.loading = false;
		}
	},
		
		// Ë∑≥ËΩ¨Âà∞Êñ∞Âª∫ËØÑ‰º∞
		goToChildInfo() {
			uni.navigateTo({
				url: '/pages/child-info/child-info'
			});
		},
		
		// Ë∑≥ËΩ¨Âà∞ÂéÜÂè≤ËÆ∞ÂΩï
		goToHistory() {
			uni.navigateTo({
				url: '/pages/history/history'
			});
		},
		
		// Ë∑≥ËΩ¨Âà∞ÁÆ°ÁêÜÂëòÈù¢Êùø
		goToDashboard() {
			uni.navigateTo({
				url: '/pages/admin/dashboard/dashboard'
			});
		},
		
		// Ë∑≥ËΩ¨Âà∞ÂàùÂßãÂåñÈ¢òÁõÆ
		goToInitQuestions() {
			uni.navigateTo({
				url: '/pages/admin/init-questions'
			});
		},
		
		// Ë∑≥ËΩ¨Âà∞‰∏™‰∫∫ËµÑÊñô
		goToUserInfo() {
			uni.navigateTo({
				url: '/uni_modules/uni-id-pages/pages/userinfo/userinfo'
			});
		},
		
		// Ë∑≥ËΩ¨Âà∞‰øÆÊîπÂØÜÁ†Å
		goToChangePwd() {
			uni.navigateTo({
				url: '/uni_modules/uni-id-pages/pages/userinfo/change_pwd/change_pwd'
			});
		},
		
		// ÈÄÄÂá∫ÁôªÂΩï
		logout() {
			uni.showModal({
				title: 'Á°ÆËÆ§ÈÄÄÂá∫',
				content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêóÔºü',
				success: (res) => {
					if (res.confirm) {
						// Ê∏ÖÈô§ÁôªÂΩï‰ø°ÊÅØ
						uni.removeStorageSync('uni_id_token');
						uni.removeStorageSync('uni_id_token_expired');
						
						// Ë∑≥ËΩ¨Âà∞È¶ñÈ°µ
						uni.reLaunch({
							url: '/pages/index/index'
						});
					}
				}
			});
		},
		
		// ËøîÂõûÈ¶ñÈ°µ
		goHome() {
			uni.switchTab({
				url: '/pages/index/index'
			});
		}
	}
}
</script>

